html {
    position: relative;
    height: 100%;
    width: 100%
}

body {
    position: relative;
    margin: 0;
    height: 100%;
    width: 100%;
    background-color: #000;
    overflow: hidden;
    background-size: cover;
    -webkit-background-size: cover;
    background-attachment: fixed;
    background-position: center center;
    background-repeat: no-repeat
}

body, p {
    font-family: Poppins, Helvetica, sans-serif;
    font-size: 14px;
    font-weight: 400;
    font-style: normal;
    line-height: 1.85em;
    color: #d5d5d5
}

body.ajax-page-visible {
    overflow: hidden
}

body::-webkit-scrollbar {
    position: absolute;
    width: 8px;
    margin-left: -8px
}

body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, .2)
}

body::-webkit-scrollbar-thumb {
    background-color: #9e9e9e;
    outline: 1px solid slategrey
}

a {
    color: #ff714a;
    text-decoration: none;
    outline: none;
    transition: all .3s ease-in-out
}

a:hover {
    color: #ff9800;
    text-decoration: none;
    outline: none
}

a:focus {
    outline: 0
}

h1, h2, h3, h4, h5, h6 {
    font-family: Poppins, Helvetica, sans-serif;
    font-weight: 600;
    font-style: normal;
    color: #f5f5f5;
    line-height: 1.5em
}

h1 {
    font-size: 32px
}

h2 {
    font-size: 27px
}

h3 {
    font-size: 21px
}

h4 {
    font-size: 18px
}

h5 {
    font-size: 16px
}

h6 {
    font-size: 14px
}

p {
    font-size: 14px;
    margin: 0 0 10px;
    font-weight: 400
}

p.lead {
    font-size: 18px;
    margin: 25px 0
}

img {
    vertical-align: bottom;
    max-width: 100%;
    height: auto;
    border: 0;
    -ms-interpolation-mode: bicubic
}

.clearfix:before, .clearfix:after {
    content: " ";
    display: table
}

.page {
    position: relative;
    width: 100%;
    height: 100%
}

.mobile-visible {
    display: none;
    visibility: hidden
}

.mobile-hidden {
    display: block;
    visibility: visible
}

.form-group {
    position: relative;
    margin: 0 0 21.5px
}

.form-control, .form-control:focus {
    height: 42px
}

.form-control, .form-control:focus, .has-error .form-control, .has-error .form-control:focus, input[type=search], input[type=password], input[type=text] {
    position: relative;
    border: 1px solid #555;
    border-radius: 0;
    display: block;
    font-size: 1em;
    margin: 0;
    padding: 10px 25px 10px 12px;
    width: 100%;
    background: 0 0;
    text-align: left;
    color: #eee;
    box-shadow: none;
    outline: none;
    font-family: poppins, Helvetica, sans-serif
}

textarea.form-control, textarea.form-control:focus {
    height: auto
}

.form-control + .form-control-border {
    content: "";
    position: absolute;
    z-index: -1;
    left: 50%;
    right: 50%;
    bottom: 0;
    background: #ff714a;
    height: 2px;
    transition: all .3s ease-in-out
}

.form-control:focus + .form-control-border {
    left: 0;
    right: 0;
    z-index: 1
}

.has-error .form-control + .form-control-border {
    background: #ff4c4c
}

.form-control::-moz-placeholder {
    color: #adadac
}

.form-control:-ms-input-placeholder {
    color: #adadac
}

.form-control::-webkit-input-placeholder {
    color: #adadac
}

.form-group .help-block {
    position: absolute;
    display: inline-block;
    padding: 0 5px;
    font-size: .93em;
    line-height: 1.75em;
    margin: -2px 0 0 10px;
    color: #fff;
    background: #ff4e4e
}

